//分类分支板块单列内容
function UnderListSubColumn(props) {
  let current = 0;
  let result = [];
  for (let i = 0; i < CATEGORY[props.num].length / 2; i++) {
    result.push(<div className="under-list-sub-column text-start">
      <a className="text-white list-item" style={{ textDecoration: "none" }}>{CATEGORY[props.num][current++]}</a>
      <a className="text-white list-item" style={{ textDecoration: "none" }}>{CATEGORY[props.num][current++]}</a>
    </div>)
  }
  return result;
}
//分类分支板块主体内容
function UnderListSubMain(props) {
  return (
    <div className="under-list-sub-row">
      <UnderListSubColumn num={props.num} />
    </div>
  )
}

